import { useState } from 'react'
import {Card,CardContent,CardMedia,Typography,Box,Grid,IconButton} from '@mui/material'
import DeleteIcon from "@mui/icons-material/Delete"
import ModalInfo from '../../Components/Modal/ModalInfo'

const CardX = ({ product }) => {

  const formatPrice = (value) => { return value.toLocaleString('es-ES', { minimumFractionDigits: 2, maximumFractionDigits: 2 }) }
  const discount = product.originalPrice ? Math.round(100 - (product.price * 100) / product.originalPrice) : null

  const [selectedProductModal, setSelectedProductModal] = useState(null)
  const [openModal, setOpenModal] = useState(false)

  const handleModalOpen = product => {
    setSelectedProductModal(product)
    setOpenModal(true)
  }

  const handleCloseModal = () => setOpenModal(false)

  return (
    <Box>
      <ModalInfo open={openModal} handleClose={handleCloseModal} product={selectedProductModal}/>
      <Card
        sx={{
          display: 'flex',
          alignItems: 'stretch',
          boxShadow: '0px 4px 10px rgba(0, 0, 0, 0.1)',
          borderRadius: 2,
          overflow: 'hidden'
        }}
      >
        <CardMedia
          component="img"
          image={product.image}
          alt={product.name}
          sx={{
            width: 150,
            height: 150,
            objectFit: 'cover',
          }}
        />

        <Box sx={{ flex: 1, px: 2, py: 1 }}>
          <CardContent sx={{ paddingBottom: '8px !important', paddingTop: 0 }}>
            <Typography variant="subtitle1" fontWeight="bold">
              {product.name}
            </Typography>

            {product.originalPrice && (
              <Typography
                variant="body2"
                sx={{ textDecoration: 'line-through', color: 'gray' }}
              >
                ${formatPrice(product.originalPrice)}
              </Typography>
            )}

            <Typography variant="h6" color="primary" fontWeight="bold">
              ${formatPrice(product.price)}{' '}
              {discount && (
                <Typography component="span" color="green" fontSize="0.9rem">
                  {discount}% OFF
                </Typography>
              )}
            </Typography>

            <Typography variant="body2" color="text.secondary">
              ltimo disponible
            </Typography>

            <Grid container spacing={2} sx={{ p: 0, mt: 1 }}>          
              <Grid item lg={4} display="flex" alignItems="center">
                <Typography sx={{ textDecoration: 'underline', color: 'black', cursor: 'pointer', fontSize: { xs: '10px', lg: '15px'} }} onClick={event => handleModalOpen(product)}>
                  Ver mas
                </Typography>
              </Grid>               
              <Grid item lg={6} display="flex" alignItems="center">
                <Typography sx={{ textDecoration: 'underline', color: 'black', cursor: 'pointer', fontSize: { xs: '10px', lg: '15px'} }} onClick={() => { console.log('Texto clickeado') }}>
                  Aadir al carrito
                </Typography>
              </Grid>        
              <Grid item lg={2} display="flex" justifyContent="end" alignItems="center">
                <IconButton color="error" sx={{ p: 0 }}> <DeleteIcon /> </IconButton>
              </Grid>        
            </Grid>             

          </CardContent>
        </Box>
      </Card>
    </Box>
  )
}

export default CardX